* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #87CEEB 0%, #98FB98 100%);
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}

#root {
  height: 100vh;
  width: 100vw;
}

.game-ui {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
  font-size: 24px;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  z-index: 100;
}

.game-over {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  z-index: 200;
  background: rgba(0, 0, 0, 0.8);
  padding: 30px;
  border-radius: 15px;
  border: 2px solid #fff;
}

.game-over h2 {
  font-size: 48px;
  margin-bottom: 20px;
  color: #ff4444;
}

.game-over p {
  font-size: 24px;
  margin-bottom: 10px;
}

.game-over button {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 15px 30px;
  font-size: 20px;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
  transition: background 0.3s;
}

.game-over button:hover {
  background: #45a049;
}

.instructions {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: white;
  text-align: center;
  font-size: 18px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  z-index: 100;
}

.start-screen {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  z-index: 200;
  background: rgba(0, 0, 0, 0.8);
  padding: 40px;
  border-radius: 20px;
  border: 3px solid #fff;
}

.start-screen h1 {
  font-size: 64px;
  margin-bottom: 20px;
  color: #4CAF50;
  text-shadow: 3px 3px 6px rgba(0, 0, 0, 1);
}

.start-screen p {
  font-size: 20px;
  margin-bottom: 30px;
  line-height: 1.4;
}

.start-screen button {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 20px 40px;
  font-size: 24px;
  border-radius: 12px;
  cursor: pointer;
  transition: background 0.3s;
  text-transform: uppercase;
  font-weight: bold;
}

.start-screen button:hover {
  background: #45a049;
}
